.movies-cardlist {
    width: 90%;
    max-width: 1140px;
    margin: 70px auto 30px;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-auto-rows: 218px;
    column-gap: 20px;
    row-gap: 32px;
    justify-content: center;
    list-style-type: none;
}

.movies-card {
    position: relative;
    width: 100%;
    max-width: 559px;
    background: #FFF;
    border-radius: 5%;
}

.movies-card__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 10px 12px 10px;
    background: #FFF;
}

.movies-card__link {
    overflow: hidden;
    display: block;
    padding: 0;
    width: 100%;
    height: 150px;
    cursor: pointer;
}

.movies-card__pic {
    width: 100%;
    height: 150px;
    object-position: center;
    object-fit: cover;
    border-top-right-radius: 5%;
    border-top-left-radius: 5%;
}

.movies-card__title {
    margin: 0;
    font-size: 13px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.movies-card__button-like {
    display: inline-block;
    cursor: pointer;
    width: 16px;
    height: 16px;
    border: 1px solid #CDD1DA;
    border-radius: 50%;
    background-color: transparent;
    transition: .2s;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
}

.movies-card__button-like_active {
    display: inline-block;
    cursor: pointer;
    width: 16px;
    height: 16px;
    border: 1px solid #CDD1DA;
    border-radius: 50%;
    background-color: transparent;
    transition: .2s;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
    background-color: #2BE080;
}

.movies-card__duration {
    margin: 0 12px 14px;
    color: #A0A0A0;
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.movies-card__button-delete {
    width: 16px;
    height: 16px;
    background-image: url("../../images/movies_card_list_delete_icon.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: contain;
    border: none;
    padding: 0;
    cursor: pointer;
}

.movies-card__button-delete:hover {
    transition: 1s linear 0.03s;
    background-image: url("../../images/movies_card_list_delete_icon_hovered.svg");
}

.movies-container {
    margin: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #FAFAFA;
}

.movies-container__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    max-width: 1140px;
    height: 36px;
    margin: 50px auto 80px;
    background: #f4f4f4;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    border-radius: 6px;
    border: none;
    cursor: pointer;
}

.movies-container__button:hover {
    transition: 1s linear 0.03s;
    background: rgba(244, 244, 244, 0.4);
    color: rgba(0, 0, 0, 0.4);
}

@media (max-width: 920px) {
    .movies-cardlist {
        margin: 86px auto 30px;
        width: 92%;
        max-width: 768px;
        grid-auto-rows: 257px;
        column-gap: 30px;
        row-gap: 36px;
    }

    .movies-card__link {
        height: 190px;
    }

    .movies-card__pic {
        height: 190px;
    }
}

@media (max-width: 620px) {
    .movies-cardlist {
        justify-items: center;
    }

    .movies-card {
        width: 300px;
    }
}

@media (max-width: 460px) {
    .movies-cardlist {
        margin: 44px auto 30px;
        grid-auto-rows: auto;
        row-gap: 20px;
    }

    .movies-card__link {
        height: 168px;
    }

    .movies-card__pic {
        height: 168px;
    }

    .movies-container__button {
        margin: 50px auto 95px;
    }
}